
.fade-enter-active, .fade-leave-active {
  -webkit-transition: opacity .5s ease;
  transition: opacity .5s ease;
}
.fade-enter, .fade-leave-active {
  opacity: 0;
}
.child-view {
  position: absolute;
  -webkit-transition: all 0.5s cubic-bezier(0.55, 0, 0.1, 1);
  transition: all 0.5s cubic-bezier(0.55, 0, 0.1, 1);
  margin-left: 282px;
}
.slide-left-enter, .slide-right-leave-active {
  opacity: 0;
  -webkit-transform: translate(30px, 0);
  transform: translate(30px, 0);
}
.slide-left-leave-active, .slide-right-enter {
  opacity: 0;
  -webkit-transform: translate(-30px, 0);
  transform: translate(-30px, 0);
}
.router-link-active {
  color: #bfd916;
}
.green-text {
  color: #bfd916;
}
.big {
  font-size: 32px;
  text-transform: uppercase;
  font-weight: bold;
}
.tiny {
  display: inline-block;
  font-size: 12px;
  line-height: 1.5;
  padding: 16px 0;
}
.box {
  position: relative;
}
.box--primary {
    background: white;
    color: black;
}
.box--secondary {
    color: white;
    background: black;
}
.copy {
  position: absolute;
  bottom: 0;
  padding: 0 20px;
  font-size: 24px;
}
.cta {
  padding: 18px 0;
  text-align: left;
}
.cta a .is-black {
    color: white;
    background: black;
}
.Button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  margin: 0;
  border: 0;
  overflow: visible;
  font: inherit;
  font-weight: 600;
  color: #333;
  text-transform: none;
  display: inline-block;
  padding: 0 32px;
  background: #ececec;
  vertical-align: middle;
  cursor: pointer;
  border-radius: 3px;
  -webkit-transition: all 0.25s easeInOutCubic;
  transition: all 0.25s easeInOutCubic;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.11), 0 4px 6px 0 rgba(0, 0, 0, 0.08);
  line-height: 36px;
  height: 36px;
  font-size: 16px;
  text-decoration: none;
  text-align: left;
  white-space: nowrap;
}
.Button--primary {
    background: black;
    color: #bfd916;
}
.Button--secondary {
    background: #bfd916;
    color: black;
}



